import { useState, useCallback } from 'react';

export interface CustomNotification {
  id: string;
  type: 'success' | 'error' | 'warning' | 'info';
  title?: string;
  message: string;
  duration?: number;
}

export function useCustomNotifications() {
  const [notifications, setNotifications] = useState<CustomNotification[]>([]);

  const addNotification = useCallback((notification: Omit<CustomNotification, 'id'>) => {
    const id = Math.random().toString(36).substr(2, 9);
    const defaultDuration = notification.type === 'error' ? 7000 : 5000;
    const newNotification = { 
      ...notification, 
      id,
      duration: notification.duration || defaultDuration
    };
    
    setNotifications((prev: CustomNotification[]) => [...prev, newNotification]);
    
    // Auto remove after duration
    if (newNotification.duration !== 0) {
      setTimeout(() => {
        removeNotification(id);
      }, newNotification.duration);
    }
    
    return id;
  }, []);

  const removeNotification = useCallback((id: string) => {
    setNotifications((prev: CustomNotification[]) => prev.filter((n: CustomNotification) => n.id !== id));
  }, []);

  const clearAllNotifications = useCallback(() => {
    setNotifications([]);
  }, []);

  // Convenience methods
  const showSuccess = useCallback((message: string, title?: string, duration?: number) => {
    return addNotification({ type: 'success', message, title: title || 'Success', duration });
  }, [addNotification]);

  const showError = useCallback((message: string, title?: string, duration?: number) => {
    return addNotification({ type: 'error', message, title: title || 'Error', duration });
  }, [addNotification]);

  const showInfo = useCallback((message: string, title?: string, duration?: number) => {
    return addNotification({ type: 'info', message, title: title || 'Info', duration });
  }, [addNotification]);

  const showWarning = useCallback((message: string, title?: string, duration?: number) => {
    return addNotification({ type: 'warning', message, title: title || 'Warning', duration });
  }, [addNotification]);

  return {
    notifications,
    addNotification,
    removeNotification,
    clearAllNotifications,
    showSuccess,
    showError,
    showInfo,
    showWarning
  };
}